@charset "utf-8";

.sold-out .ch-area {
  input + label {display: none;}
  .check {
    width: 6.133vw;
    height: 6.133vw;
    background: url('/_ui/img/m/icn/icn-check05.png') no-repeat;
    background-size: 100%;
  }
}
.cart {
  .top {
    overflow: hidden;
    width: 90.4vw;
    line-height: 6.133vw;
    margin: 6.933vw auto;
    padding-bottom: 4.266vw;
    border-bottom: .8vw #000 solid;
    .ch-area {
      float: left;
      width: 30vw;
    }
    .btn-del {
      float: right;
      font-size: 3.733vw;
    }
  }
  .cont {
    .empty {
      padding: 38.933vw 0;
      color: #aaa;
      font-size: 3.733vw;
      text-align: center;
    }
    .c-list {
      margin-top: 9.6vw;
      background: #f7f7f7;
      li {
        margin-top: 2.133vw;
        background: #fff;
        padding: 4.8vw;
        &:first-child {
          margin-top: 0;
          padding-top: 0;
        }
      }
      .setItem {
        // border-bottom: .266vw solid #f1f1f1;
        .info {
          padding-bottom: 0;
        }
        .btn-set-option {
          margin: 4vw 0;
          a {
            display: block;
            font-family: "Noto Sans KR";
            font-size: 3.2vw;
            color: #1f1f1f;
            &::after {
              content: '';
              display: inline-block;
              width: 1.733vw;
              height: 1.2vw;
              margin-left: 1.6vw;
              background: url('/_ui/img/m/icn/icn-arrow05.png') no-repeat;
              background-size: contain;
              vertical-align: middle;
            }
            &.on {
              &::after {
                transform: rotate(-180deg);
                -webkit-transform: rotate(-180deg);
                -ms-transform: rotate(-180deg);
                -moz-transform: rotate(-180deg);
              }
            }
          }
        }
        .set-option-list {
          display: none;
          clear: both;
          padding-top: 6.933vw;
          border-top: .266vw solid #f1f1f1;
          .list {
            float: left;
            padding-bottom: 3.466vw;
          }
        }
        .set-option-list.on {
          display: block;
        }
        .info .prod-add .conts {
          float: none;
        }
      }
      .setItem ~ .info .delivery-info {
        width: 100%;
        margin-top: 0;
        margin-left: 25.33vw;
      }
      .top {
        margin: 0 0 2.933vw;
        padding-bottom: 0;
        border: 0;
        .btn-del {
          display: block;
          width: 4vw;
          height: 4vw;
          background: url('/_ui/img/m/icn/icn-close.png') no-repeat;
          background-size: 100%;
          font-size: 0;
          text-indent: -9999px;
          margin-top: 1.066vw;
        }
      }
      .prod {
        float: left;
        position: relative;
        width: 25.333vw;
        img {
          width: 100%;
        }
        a {
          &::before {
            content: '';
            display: block;
            position: absolute;
            opacity: .03;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: #000;
            z-index: 1;
          }
        }
      }
      .info {
        float: left;
        width: 61.066vw;
        margin-left: 4vw;
        padding-bottom: 6.666vw;
        .t1 {
          display: inline-block;
          width: 100%;
          margin: 1.333vw 0 2.666vw;
          .brand {
            font-family: 'Helvetica';
            font-size: 2.666vw;
            font-weight: 900;
            color: #1f1f1f;
          }
          .brand + span {
            font-size: 2.4vw;
            float: right;
            color: #bbb;
          }
        }
        .t2 {
          font-family: "Noto Sans KR";
          font-size: 3.733vw;
          height: 8.666vw;
          margin-bottom: 1.333vw;
          color: #1f1f1f;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-wrap: normal;
          a {
            font-family: "Noto Sans KR";
            color: #1f1f1f;
          }
        }
        .option {
          font-size: 3.733vw;
          margin-bottom: 1.333vw;
          color: #1f1f1f;
        }
        .btm {
          position: relative;
          font-size: 3.466vw;
          .discount {
            margin-right: 2.666vw;
            color: #9ba4b0;
            text-decoration: line-through;
          }
          .price {
            color: #1f1f1f;
          }
          .family-btn-o {
            position: absolute;
            display: block;
            width: 20vw;
            height: 6.133vw;
            bottom: 0;
            right: 0;
            margin-top: -2.666vw;
            color: #fff;
            font-size: 3.2vw;
            line-height: 6.133vw;
            text-align: center;
            background: #f23d4e;
          }
          .family-btn-x {
            position: absolute;
            display: block;
            width: 20vw;
            height: 6.133vw;
            bottom: 0;
            right: 0;
            margin-top: -1.333vw;
            font-size: 3.2vw;
            color: #fff;
            line-height: 6.133vw;
            text-align: center;
            background: #bbb;

          }
          .family {
            display: block;
            font-size: 3.466vw;
            color: #f85262;
            margin: 1.333vw 0 3.6vw;
          }
        }
        .prod-add {
          clear: both;
          margin-top: 3.6vw;
          position: relative;
          .sizeSelect,
          button {
            float: left;
            height: 6.266vw;
            width: 25.1vw;
            border-bottom: .266vw #f1f1f1 solid;
            line-height: 6.266vw;
            font-size: 3.733vw;
            margin-right: 6.333vw;
            padding-right: 2.4vw;
            padding-left: 1vw;
            font-weight: 700;
            background: url('/_ui/img/m/icn/icn-arrow04.png') no-repeat 95% 50%;
            background-size: 2.4vw;
          }
          .sizeSelect {
            text-overflow: ellipsis;
            &.disabled {
              background: url('/_ui/img/m/icn/icn-arrow07.png') no-repeat 95% 50%;
              background-size: 2.4vw;
              span {
                opacity: .2;
              }
            }
          }
          .layer-size-select.on {
            display: block;
          }
          .layer-size-select {
            display: none;
            position: absolute;
            top: 6.266vw;
            left: -1px;
            min-width: 28.5vw;
            max-height: 41.33vw;
            z-index: 1;
            background: #fff;
            border: 1px solid #666;
            overflow-y: scroll;
            span {
              display: list-item;
              background: none;
              margin: 0;
              padding: 1vw;
              min-height: 6.266vw;
              font-size: 3.733vw;
              &.disabled,
              &:hover,
              &:focus {
                background: #dedede;
              }
            }
            &::-webkit-scrollbar {
              width: 4px;
              height: 4px;
              background-color: rgba(255, 255, 255, 0);
            }
            &::-webkit-scrollbar-thumb {
              position: relative;
              background: #666;
            }
          }
          button {
            position: absolute;
            top: 0;
            text-align: left;
          }
          .conts {
            float: left;
            width: 26vw;
            height: 6.266vw;
            border-bottom: .266vw #f1f1f1 solid;
            position: relative;
            .quantity {
              width: 100%;
              font-size: 3.733vw;
              line-height: 6vw;
              text-align: center;
            }
            a {
              position: absolute;
              top: 0;
              width: 4.933vw;
              height: 6vw;
              font-size: 0;
              text-indent: -999px;
            }
            .bt-minus {
              left: 0;
              background: url('/_ui/img/m/icn/icn-minus.png') no-repeat center;
              background-size: 2.933vw;
            }
            .bt-plus {
              right: 0;
              background: url('/_ui/img/m/icn/icn-plus.png') no-repeat center;
              background-size: 2.933vw;
            }
            &.disabled {
              a,
              .quantity {
                opacity: .2;
              }
            }
          }
        }
        .delivery-info {
          display: inline-block;
          font-size: 3.466vw;
          color: #898989;
          margin-top: 5.333vw;
          line-height: 5.066vw;
          width: 100%;
          dt {
            float: left;
            width: 17.866vw;
          }
          dd {
            float: left;
            width: 43.2vw;
          }
        }
      }
      .btn-area {
        overflow: hidden;
        clear: both;
        a {
          display: block;
          float: left;
          width: 44vw;
          height: 10.933vw;
          line-height: 10.933vw;
          font-size: 3.466vw;
          text-align: center;
          font-weight: 700;
          box-sizing: border-box;
        }
        .btn1 {
          color: #1f1f1f;
          border: .266vw #f1f1f1 solid;
          margin-right: 2.4vw;
        }
        .btn2 {
          color: #fff;
          background: #1f1f1f;
        }
        .btn3 {
          background: #f2f2f2;
          color: #ccc;
        }
      }
    }
    .gift {
      background: #f7f7f7;
      h3 {
        font-size: 3.733vw;
        padding: 9.066vw 4.8vw 2.933vw;
        color: #1f1f1f;
        font-weight: 700;
      }
      .disc {
        font-size: 3.466vw;
        padding: 0 4.8vw;
        color: #898989;
        line-height: 4.8vw;
        letter-spacing: -.4px;
      }
      .disc + .disc {
        margin-top: 2vw;
      }
      .gift-choice {
        padding-bottom: 9.333vw;
        img {
          width: 100%;
        }
        li {
          padding: 0 2vw;
          margin-top: 9.333vw;
          &:first-child {
            margin-top: 0;
          }
          a {
            display: block;
          }
          .tit {
            margin-top: 9.333vw;
            font-size: 3.466vw;
            color: #1f1f1f;
          }
          .list {
            margin-top: 2.933vw;
            li {
              margin-top: 2.933vw;
              overflow: hidden;
              cursor: pointer;
              background: #fff;
              &:first-child {
                margin-top: 0;
              }
              &.select {
                background: #fff url('/_ui/img/m/icn/icn-check03.png') no-repeat 82.933vw 9.866vw;
                background-size: 2.933vw 2.133vw;
              }
            }
            .prod {
              display: inline-flex;
              display: -webkit-inline-flex;
              display: -ms-inline-flex;
              justify-content: center;
              -webkit-justify-content: center;
              align-items: center;
              -webkit-align-content: center;
              width: 19.733vw;
              min-height: 16vw;
              margin: 1.066vw 4vw 1.066vw 0;
              vertical-align: middle;
              img {
                width: 100%;
                font-size: 2.133vw;
              }
            }
            .info {
              display: inline-block;
              width: 54.066vw;
              vertical-align: middle;
              .t1 {
                font-size: 2.666vw;
                font-weight: 700;
                color: #1f1f1f;
              }
              .t2 {
                font-size: 2.933vw;
                margin-top: 1.066vw;
                color: #898989;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
              }
            }
          }
        }
      }
    }
  }
}
.cart + .notice.empty {
  background: #f7f7f7;
  h3 {
    color: #aaa;
  }
}
.cart + .notice {
  color: #aaa;
  padding: 8.8vw 0 9.6vw;
  h3 {
    padding: 0 4.8vw;
    font-size: 3.733vw;
    color: #1f1f1f;
    font-weight: 700;
  }
  ul {
    margin-top: 4vw;
    padding: 0 4.2vw;
    li {
      font-size: 3.466vw;
      &:first-child {
        margin-top: 0;
      }
    }
  }
}
.total-price {
  padding: 0 4.8vw;
  border-top: .266vw #f1f1f1 solid;
  dl {
    padding: 9.6vw 0;
    font-size: 3.466vw;
    overflow: hidden;
    color: #898989;
    dt {
      height: 7.333vw;
      line-height: 7.333vw;
      float: left;
      width: 50%;
    }
    dd {
      height: 7.333vw;
      line-height: 7.333vw;
      float: left;
      width: 50%;
      text-align: right;
    }
    .total {
      font-size: 4vw;
      color: #1f1f1f;
      margin-bottom: 8vw;
    }
  }
}
.btn-order {
  display: block;
  width: 90.4vw;
  margin: 0 auto 9.333vw;
  height: 13.6vw;
  line-height: 13.6vw;
  text-align: center;
  color: #fff;
  font-weight: 700;
  font-size: 4vw;
  background: #f23d4e;
}
.total-price-fixed {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  margin: 0 auto;
  overflow: hidden;
  max-width: 750px;
  min-width: 320px;
  width: 100%;
  z-index: 2;
  div {
    width: 60.533vw;
    height: 13.6vw;
    line-height: 13.6vw;
    background: #191919;
    float: left;
    span {
      color: #fff;
      font-size: 3.2vw;
      opacity: .5;
      margin-left: 4.8vw;
    }
    span + span {
      font-size: 4.266vw;
      opacity: 1;
      font-weight: 700;
      margin-left: 2.8vw;
    }
  }
  .btn-order {
    width: 39.466vw;
    float: left;
    margin: 0;
  }
}
.popup-wow-guide {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 100vw;
  max-width: 450px;
  transform: translate(-50%, -50%);
  z-index: 1000;
  a,
  img {
    display: block;
    width: 100%;
  }
  .wow-close-btn {
    position: absolute;
    top: 5%;
    right: 5%;
    width: 6vw;
    height: 6vw;
    max-width: 27px;
    max-height: 27px;
    background: url(/_ui/img/pc/btn-pop-cls.png) no-repeat;
    background-size: 100%;
  }
  .wow-cookie-btn {
    padding: 2% 0;
    background-color: #000;
    img {
      width: 50%;
      margin: 0 auto;
    }
  }
}
