  .sold-out .ch-area {
    .check {
      width: 46px;
      height: 46px;
    }
  }
  .cart {
    .top {
      width: 678px;
      line-height: 46px;
      margin: 52px auto;
      padding-bottom: 32px;
      border-bottom: 6px #000 solid;
      .ch-area {
        width: 225px;
      }
      .btn-del {
        font-size: 28px;
      }
    }
    .cont {
      .empty {
        padding: 292px 0;
        font-size: 28px;
      }
      .c-list {
