              padding-bottom: 26px;
            }
          }
        }
        .setItem ~ .info .delivery-info {
          margin-left: 190px;
        }
        .top {
          margin: 0 0 22px;
          .btn-del {
            width: 30px;
            height: 30px;
            margin-top: 8px;
          }
        }
        .prod {
          width: 190px;
        }
        .info {
          width: 458px;
          margin-left: 30px;
          padding-bottom: 50px;
          .t1 {
            margin: 10px 0 20px;
            .brand {
              font-size: 20px;
            }
            .brand + span {
              font-size: 18px;
            }
          }
          .t2 {
