            font-size: 26px;
            .discount {
              margin-right: 20px;
            }
            .family-btn-o {
              font-size: 24px;
              width: 150px;
              height: 46px;
              line-height: 46px;
              margin-top: -20px;
            }
            .family-btn-x {
              font-size: 24px;
              width: 150px;
              height: 46px;
              line-height: 46px;
              margin-top: -10px;
            }
            .family {
              font-size: 26px;
              margin: 10px 0 27px;
            }
          }
          .prod-add {
            margin-top: 27px;
            .sizeSelect {
              height: 47px;
              width: 188px;
              border-bottom: 2px #f1f1f1 solid;
              line-height: 47px;
              font-size: 28px;
              margin-right: 47px;
              padding-right: 18px;
              padding-left: 7px;
              background-size: 18px;
              &.disabled::after {
                height: 47px;
                width: 214px;
              }
            }
            .layer-size-select {
              top: 47px;
              min-width: 213px;
              max-height: 315px;
              span {
                padding: 8px;
                min-height: 47px;
                font-size: 28px;
              }
            }
            .conts {
              width: 195px;
              height: 47px;
              border-bottom: 2px #f1f1f1 solid;
