              li {
                margin-top: 22px;
                &.select {
                  background: #fff url('/_ui/img/m/icn/icn-check03.png') no-repeat 622px 74px;
                  background-size: 22px 16px;
                }
              }
              .prod {
                width: 148px;
                min-height: 120px;
                margin: 8px 30px 8px 0;
