    width: 678px;
    margin: 0 auto 70px;
    height: 102px;
    line-height: 102px;
    font-size: 30px;
  }
  .total-price-fixed {
    div {
      width: 454px;
      height: 102px;
      line-height: 102px;
      span {
        font-size: 24px;
        margin-left: 36px;
      }
      span + span {
        font-size: 32px;
        margin-left: 21px;
      }
    }
    .btn-order {
      width: 296px;
    }
  }
}
